.clients {
    max-width: 100%;
    color: $primalSecondColor;
    background: $primalSecondBGColor;
    padding-bottom: 87px;

    &-container {
        max-width: 100%;
        display: flex;
        flex-direction: column;
    }

    &-header {
        display: flex;
        justify-content: center;

        &__text {
            @include primalFontSemiBold;
            font-size: $clientsHeaderFontSize;
            
            @media screen and (max-width: 560px) {
                font-size: calc($clientsHeaderFontSize / 1.3)
            }
        }
    }

    &-content {
        padding-top: 36px;

        .clients-slider {
            overflow: scroll;
            max-width: 100%;
                &::-webkit-scrollbar{
                    display: none;
                }
            &__line {
                display: flex;
                justify-content: space-between;
                position: relative;
                width: 200%;
            }

            .clients-card {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-basis: 206px;
                height: 230px;
                background: $primalBGColor;
                user-select: none; 
                    &__img{
                        pointer-events: none
                    }

                @media screen and (max-width: 992px) {
                    flex-basis: 130px;
                }

                @media screen and (max-width: 940px) {
                    flex-basis: 110px;
                }

                @media screen and (max-width: 800px) {
                    flex-basis: 100px;
                }

                &:nth-child(1n+5) {
                    @media screen and (max-width: 768px) {
                        display: none;
                    }
                }

                @media screen and (max-width: 768px) {
                    flex-basis: 24%;
                }

                &__img {
                    @media screen and (max-width: 992px) {
                        max-width: 80%;
                    }
                }
            }
        }
    }
}